<template>
  <div class="logout">
    <h1>Cerrando Session</h1>
  </div>
</template>
<script>
import User from '@/api/User'

export default {
  name: 'Logout',
  mounted () {
    User.logout().then((response) => {
      if(response.data.status === 'OK'){
        this.$router.push({path: '/'})
      }
    }).catch((errors) => {
      console.log(errors)
    })
  }
}
</script>
